<template>
	<view>
		<view>
			<view class="form-box">
				<uni-forms label-position="top">
					<uni-forms-item label="主题" name="theme">
						<uni-easyinput type="text" v-model="formData.theme" placeholder="请输入主题" />
					</uni-forms-item>
					<uni-forms-item label="内容" name="content">
						<uni-easyinput type="text" v-model="formData.content" placeholder="请输入内容" />
					</uni-forms-item>
					<uni-forms-item label="收入\n(支出)" name="price">
						<uni-easyinput type="number" v-model="formData.price" placeholder="请输入收入(支出)" />
					</uni-forms-item>
				</uni-forms>
				<button type="primary" @click="submit('valiForm')">保存</button>

				<template v-if="formData._id">
					<view class="util-box" style="margin-top: 20px;">
						<button type="info" @click="remove">
							删除记事
						</button>
					</view>
				</template>
			</view>


		</view>

	</view>
</template>

<script>
	const db = uniCloud.database()
	export default {
		onLoad(option) {
			this.createAt = option.date
			if (option.id) {
				this.model = 'edit'
				this.formData.id = option.id
				this.getInfo(option.id)
			}
		},
		data() {
			return {
				id: '',
				model: 'add',
				formData: {
					_id: '',
					theme: '',
					content: '',
					price: 0,
				},
				createAt: ''
			}
		},

		methods: {
			async submit() {
				const createAt = this.createAt
				this.addData()
				const {
					result
				} = await uniCloud.callFunction({
					name: 'item-co',
					data: {
						_id: this.formData._id,
						theme: this.formData.theme,
						content: this.formData.content,
						price: this.formData.price,
						createAt: createAt,
						userId: uni.getStorageSync('uid')
					}
				})
				if (!result) {
					uni.showToast({
						title: '添加失败，请重试',
					})
					return
				}
				uni.$emit('newItemEvent', {
					newItemId: result
				})
				uni.navigateBack()
			},
			addData() {
				db.collection("history").add({
					theme: this.formData.theme,
					content: this.formData.content,
					price: this.formData.price,

				}).then(res => {
					console.log(res)
					uni.hideLoading()
					uni.showToast({
						title: "保存成功"
					})
					setTimeout(() => {
						uni.switchTab({
							url: "/pages/index/index"
						})
					}, 800)
				})
			},
			async remove() {
				const {
					result
				} = await uniCloud.callFunction({
					name: 'item-co',
					data: {
						_id: this.formData._id,
						model: 'remove'
					},
				})
				uni.$emit('newItemEvent', {
					newItemId: result
				})
				uni.navigateBack()
			},
			async getInfo(id) {
				uni.showLoading()

				const {
					result
				} = await uniCloud.callFunction({
					name: 'item-info-co',
					data: {
						id,
						date: this.createAt
					}
				})

				uni.hideLoading()

				if (result) {
					this.formData = {
						...result
					}
				}
			}
		}
	}
</script>

<style>
	.form-box {
		padding: 16px;
		box-sizing: border-box;
	}
</style>